import React from "react";
import { makeStyles } from "@material-ui/core";

import Image from "../img/About.jpg";
import myself from "../img/square_me.jpg";
import { Paper, Typography } from "@material-ui/core";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

const useStyles = makeStyles(theme => ({
  introWrapper: {
    backgroundImage: `url(${Image})`,
    minHeight: "100%",
    [theme.breakpoints.down('md')]: {
      backgroundSize: "auto 100%",
    },
    [theme.breakpoints.up('md')]: {
      backgroundSize: "100%",
    },
    backgroundRepeat: "no-repeat",
    backgroundAttachment: "fixed",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    flexDirection: "column",
    paddingBottom: theme.spacing(11)
  },
  introContent: {
    padding: theme.spacing(3),
    maxWidth: "60%",
    display: "flex",
  },
  imgContent: {
    maxHeight: "30vh",
    width: "auto",
  },
  paragraph: {
    paddingLeft: theme.spacing(3)
  },
  eyeCatcher: {
    color: "white",
    textAlign: "center",
    padding: theme.spacing(30),
    paddingBottom: theme.spacing(27)
  },
  downIcon: {
    margin: theme.spacing(10),
    marginTop: -theme.spacing(13),
  }
}))

const About = () => {
  const classes = useStyles();

  return (
    <div>
      <div className={classes.introWrapper}>
        <Typography variant="h1" className={classes.eyeCatcher}>
          Hi! <br/> Welcome to Hanchi's website
        </Typography>
        <FontAwesomeIcon icon={["fas", "chevron-down"]} size="3x" color="white" className={classes.downIcon} />
        <Paper className={classes.introContent}>
          <img src={myself} alt="this is me" className={classes.imgContent} />
          <div className={classes.paragraph}>
            Hi! Welcome to my website. I am Tony Hanchi Zhang, a junior studying computer science @ University of Michigan - Ann Arbor. (Go Blue!)
            <br/>
            <br/>
            I have a great passion about working in the computer science field and am currently looking for a software development internship in 2020 summer. My main interests are frontend / fullstack development and infrastructure development.
            <br/>
            <br/>
            I am also interested in doing research that focuses more on the engineering side of the subject. Check out what we built over the past summer under Experience!
            <br/>
            <br/>
            Part of the initiative about building this website is to show those that are interested in hiring me something more than what I presented on my resume; however, the more important thing is I enjoy applying the skills I have learned to creating a website and experimenting with various new things I want to learn about.
            <br/>
            <br/>
            This is still under construction, but have fun exploring!
            
          </div>
        </Paper>
      </div>
    </div>
    
  )
}

export default About;