import React, { useRef } from "react";
import { BottomNavigation, BottomNavigationAction, makeStyles, Typography } from "@material-ui/core";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(2),
  },
  navItem: {
    width: "30%"
  },
  wrapper: {
    marginTop: theme.spacing(5),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "center"
  }
}))

const BottomNav = () => {
  const classes = useStyles();

  return (
    <div className={classes.wrapper}>
      <Typography variant="caption" align="center">
        Find me here!
      </Typography>
      <BottomNavigation
        value={null}
        className={classes.root}
      >
        <BottomNavigationAction 
          label="Github" 
          value="github" 
          icon={<FontAwesomeIcon icon={['fab', 'github']} size="2x" fixedWidth />} 
          className={classes.navItem} 
          target="_blank" 
          href="https://github.com/tonyzhc" 
        />
        <BottomNavigationAction 
          label="Linkedin" 
          value="linkedin" 
          icon={<FontAwesomeIcon icon={['fab', 'linkedin']} size="2x" fixedWidth />} 
          className={classes.navItem} 
          target="_blank"
          href="https://www.linkedin.com/in/tonyhanchizhang/"
        />
        <BottomNavigationAction 
          label="Contacts" 
          value="contacts" 
          icon={<FontAwesomeIcon icon={['fas', 'envelope']} size="2x" fixedWidth />} 
          className={classes.navItem}
          target="_blank"
          href="mailto:tonyzhc@umich.edu"
        />
      </BottomNavigation>
      {/* <p>powered by React</p> */}
    </div>
  )
}

export default BottomNav;